@keyframes eyelashanimation{

    0%{
        opacity: 0;
    }
    20%{
        opacity: 0;
    }
    21%{
        opacity: 1;
    }
    21.8%{
        opacity: 1;
    }
    22.5%{
        opacity: 0;
    }
    50%{
        opacity: 0;

    }
    51%{
        opacity: 1;
    }
    51.8%{
        opacity: 1;
    }
    52.5%{
        opacity: 0;
    }
    90%{
        opacity: 0;
    }
    91%{
        opacity: 1;
    }
    94%{
        opacity: 1;
    }
    95%{
        opacity: 0;
    }
    96%{
        opacity: 0;
    }
    97%{
        opacity: 1;
    }
    98%{
        opacity: 1;
    }
    99%{
        opacity: 0;
    }
    100%{
        opacity: 0;
    }

}
@keyframes eyeanimation {

        0% {
            transform: translateX(1px);
        }
    
        100% {
            transform: translateX(-1px);
        }
    } 


@keyframes moveLogo-left {
    from{transform: translate3d(0,-50%,0);// translateY(-50%) translate(0);
    }
    to{transform:  translate3d(-100%,-50%,0);}//translateY(-50%) translate(100%);}
}
@keyframes moveLogo-left2 {
    from{transform: translate3d(100%,-50%,0);}//translateY(-50%) translate(-100%);}
    to{transform:translate3d(0,-50%,0);}//translateY(-50%) translate(0);}
}
