@keyframes movecloud {
    from{transform: scaleX(2) ;//rotateX(180deg);// translateY(-50%) translate(0);
    }
    to{transform:  scaleX(5) };//rotateX(10deg)}//translateY(-50%) translate(100%);}
}



@keyframes improvedFlashing {
    0%, 100% {
        opacity: 0;
    }
    50% {
        opacity: 1;
    }
}



@keyframes flashing {

    0% {
        opacity: 0;
    }
    
    9% {
        opacity: 1;
    }
    41% {
        opacity: 1;
    }
    45% {
        opacity: 0.6;
    }
    
    51% {
        opacity: 1;
    }
    61% {
        opacity: 1;
    }
        
    87% {
        opacity: 0;
    }

    90% {
        opacity: 0;
    }
} 

@keyframes windMovePaper {
    0%{
        transform: rotateY(-7deg);
    }
    10%{
        transform: rotateY(-5deg);
    }    
    20%{
        transform: rotateY(-7deg);
    }
    30%{
        transform: rotateY(-5deg);
    }
    40%{
        transform: rotateY(-6deg);
    }
    60%{
        transform: rotateY(-5deg);
    }
    80%{
        transform: rotateY(-7deg);
    }
    90%{
        transform: rotateY(-5deg);
    }
}

// animation: windMovePaper 8s ease-in-out infinite;
@keyframes moveArrows {
    0%{
        transform:  translate3d(0px,0px, 0);
    }
    10%{
        transform:  scale(1.04) translate3d(-1px,-1px, 0);
    }    
    20%{
        transform:  translate3d(1px,1px, 0);
    }
    30%{
        transform:  translate3d(-1px,-1px, 0);
    }
    40%{
        transform:  translate3d(1px,-0px, 0);
    }
    60%{
        transform:  scale(1) translate3d(-1px,-1px, 0);
    }
    80%{
        transform:  translate3d(0px,1px, 0);
    }
    90%{
        transform:  translate3d(1px,-1px, 0);
    }
}

