        // rgba(255, 255, 255, 0.2) ,
        // white 40%,
        // rgb(179, 179, 179), 
        // white 66%,
        // white
        // );
    }
}
@keyframes arrowAfterEffect {
    from{
        transform:  translate3d(0,-95%,0);
    }
    to{
        transform: translate3d(0,-1%,0);
    }
}
@keyframes arrowEffect{
    0%{
        opacity: 1;
        transform: translate3d(0,-48px,0);
    }
    20%{
        opacity: 1;
        transform: translate3d(0,-48px,0);
    }
    45%{
        opacity: 0;
        transform: translate3d(0,-32px,0) scale(.90);
    }
    50%{
        opacity: 0;
        transform: translate3d(0,-48px,0);

    }
    80%{
        opacity: 1;
        transform: translate3d(0,-48px,0);
    }
    100%{
        opacity: 1;
        transform: translate3d(0,-48px,0);
    }


}
